.footer {
  width: 100%;
  padding: 63px 0;
  background-color: $icons-grey;
  margin-top: auto;
  &__container {
    display: flex;
    justify-content: space-between; }
  &__logo {
    display: block; }
  &-contacts {
    padding-left: 50px;
    border-left: 1px solid $disabled-grey;
    @include mw(1300) {
      display: none; }
    &__title {
      font-size: 20px;
      line-height: 1.4;
      color: $light-grey;
      margin-bottom: 12px; }
    &__soc-list {
      display: flex;
      margin-bottom: 35px; }
    &__soc-item {
      margin-right: 10px; }
    &__copyright {
      font-size: 14px;
      line-height: 18px;
      color: $disabled-grey;
      span {
        display: block; } } }
  &__nav {
    width: 100%;
    max-width: 720px;
    @include mw(1300) {
      display: none; }
    &-list {
      display: flex;
      flex-wrap: wrap;
      @include mw(1300) {
        justify-content: flex-end; } }
    &-item {
      width: 25%;
      max-width: 150px;
      margin-bottom: 15px;
      a {
        font-size: 14px;
        line-height: 18px;
        color: #EFEFEF; }
      > a {
        display: block;
        min-height: 40px;
        margin-bottom: 8px;
        font-size: 16px;
        line-height: 20px;
        font-weight: bold;
        color: $yellow; } } }
  &__sub-nav-item {
    line-height: 1;
    margin-bottom: 15px; } }
