.logo {
  display: inline-block;

  &_header {
    width: 210px;
    height: 33px; }

  &_footer {
    width: 210px;
    height: 160px; }

  &_rect {
    @include b_image('icons/logo_rect.svg'); }

  &_sq {
    @include b_image('icons/logo_sq.svg'); } }

.soc {
  display: inline-block;
  width: 40px;
  height: 41px;

  &_inst {
    @include b_image('icons/inst.svg'); }

  &_fb {
    @include b_image('icons/fb.svg'); } }

.icon {
  display: inline-block;
  width: 88px;
  height: 88px;

  &_pdf {
    @include b_image('icons/pdf.svg'); }

  &_docx {
    @include b_image('icons/docx.svg'); }

  &_xlsx {
    @include b_image('icons/xlsx.svg'); }

  &_txt {
    @include b_image('icons/txt.svg'); }

  &_database {
    @include b_image('icons/database.svg'); }

  &_hook {
    @include b_image('icons/hook.svg'); }

  &_paper {
    @include b_image('icons/paper.svg'); }

  &_protractor {
    @include b_image('icons/protractor.svg'); } }
